  width: 36px;
  height: 36px;
  background-color: var(--background-ligth-color);
  border: 3px solid var(--text-red-color);
  cursor: pointer;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s ease-in-out;
}

